import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import ButtonBase from "@mui/material/ButtonBase";

const dispatchedEvents = [];

const SrsMuiButtonBase = function (props, ref) {
  return (
    <ButtonBase
      disabled={props.disabled}
      onClick={props.onClick}
      component={props.component}
      theme={props.muiTheme}
    >
      <slot />
    </ButtonBase>
  );
};

const styles = `
  /* Custom styles for the MuiButtonBase component */
`;

const props = [
  {
    name: "disabled",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "onClick",
    defaultValue: null,
    type: "function",
  },
  {
    name: "component",
    defaultValue: "button",
    type: "string",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to ButtonBase if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiButtonBase),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
